<template>
  <div class="answer_sheet_header">
    <table>
      <tr>
        <td colspan="2">姓名：</td>
        <td rowspan="6">
          <table>
            <tr>
              <td :colspan="exam_no_num">准考证号</td>
            </tr>
            <tr class="fixed_row">
              <td v-for="index in exam_no_num" :key="index"></td>
            </tr>
            <tr class="use_answer_sheet_font">
              <td v-for="index in exam_no_num" :key="index">0<br>1<br>2<br>3<br>4<br>5<br>6<br>7<br>8<br>9</td>
            </tr>
          </table>
        </td>
      </tr>
      <tr>
        <td colspan="2">班级：</td>
      </tr>
      <tr>
        <td>正确填涂：</td>
        <td class="use_answer_sheet_font">u  </td>
      </tr>
      <tr>
        <td>错误填涂：</td>
        <td class="use_answer_sheet_font">v w x y</td>
      </tr>
      <tr>
        <td>缺考标记：</td>
        <td class="use_answer_sheet_font">`</td>
      </tr>
      <tr>
        <td colspan="2">注意事项：<br>填涂时用2B铅笔将选项涂满涂黑。修改时用橡皮擦干净。请注意题号顺序。请保持答题卡整洁，不要折叠、乱作标记。</td>
      </tr>
    </table>
  </div>
</template>

<script>
import UnitHandler from '../utils/units.js'

export default {
  name: 'AnswersheetHeader',
  props: {
    exam_no_num: Number
  },
  data () {
    return {
      real_height: 0
    }
  },
  updated: function () {
    var computed_height = parseFloat(
      document.defaultView.getComputedStyle(this.$el).height)
    this.real_height = computed_height / UnitHandler.get_dpi()[1] * 25.4
    console.log('real height of answersheet header' + ' is ' + this.real_height.toFixed(2))
    this.$emit('render_finished', {component: this, real_height: this.real_height})
  }
}
</script>

<style>
  .answer_sheet_header {
    font-size: 3mm;
  }

  .answer_sheet_header > table, .answer_sheet_header > table > tr, .answer_sheet_header > table > tr > td {
    border-collapse: collapse;
    border: 1px solid black;
    text-align: left;
  }

  .answer_sheet_header > table table {
    border-collapse: collapse;
    border: 1px solid black;
    text-align: center;
    margin: 2mm;
  }

  .answer_sheet_header > table tr {
    border-collapse: collapse;
    border: 1px solid black;
    text-align: center;
    min-height: 5mm;
  }

  .answer_sheet_header > table td {
    border-collapse: collapse;
    border: 1px solid black;
    text-align: center;
  }

  .use_answer_sheet_font {
    font-family: answersheet;
    font-size: 5mm;
  }
</style>



