  },
  mounted: function () {
    var column1 = this.render_part(
      AnswersheetColumn,
      {
        propsData: {
          position_no: 1,
          paper_size: this.paper_size,
          columns_num: this.columns_num
        }
      },
      document.getElementById('answersheet_page_1')
    )
    this.created_columns[this.created_columns.length] = column1
    this.columns_info[this.created_columns.length - 1] = []

    this.render_part(
      AnswersheetTitle,
      {
        propsData: {
          title: this.answersheet_structure.title
        }
      },
      column1.$el
    )
    this.render_waiting_list[this.render_waiting_list.length] = [
      AnswersheetHeader,
      {
        propsData: {
          exam_no_num: this.exam_no_num
        }
      }
    ]

    var areas_with_same_type = this.handle_answersheet_structure()
    for (var index_1 in areas_with_same_type) {
      this.render_waiting_list[this.render_waiting_list.length] = [
        AnswersheetType,
        {
          propsData: {
            title: areas_with_same_type[index_1].big_title
          }
        },
        false
      ]
      for (var index_2 in areas_with_same_type[index_1].questions_division) {
        if (areas_with_same_type[index_1].questions_division[index_2][0].IsSubjective === 1) {
          this.render_waiting_list[this.render_waiting_list.length] = [
            AnswersheetSubjective,
            {
              propsData: {
                raw_data: areas_with_same_type[index_1].questions_division[index_2]
              }
            },
            false
          ]
        } else {
          this.render_waiting_list[this.render_waiting_list.length] = [
            AnswersheetProblem,
            {
              propsData: {
                title: '小题' + areas_with_same_type[index_1].questions_division[index_2][0].BigTile
              }
            },
            false
          ]
          this.render_waiting_list[this.render_waiting_list.length] = [
            AnswersheetObjective,
            {
              propsData: {
                height: 50
              }
            },
            true
          ]
        }
      }
