  },
  methods: {
    complete_type: function (index) {
      return this.chn_number[index] + '、' + this.answersheet_structure.titleModels[index].TitleType
    },
    render_part: function (vue_module, in_paras, owner) {
      const Cls = Vue.extend(vue_module)
      const ins = new Cls(in_paras)
      ins.$mount()
      owner.appendChild(ins.$el)
      ins.$forceUpdate()
      ins.$on('render_finished', this.render_finished)
      ins.$on('rerender', this.rerender_trigger)
      return ins
    },
    render_part_first: function (vue_module, in_paras, owner) {
      const Cls = Vue.extend(vue_module)
      const ins = new Cls(in_paras)
      ins.$mount()
      owner.insertBefore(ins.$el, owner.firstChild)
      ins.$on('render_finished', this.render_finished)
      ins.$on('rerender', this.rerender_trigger)
      return ins
    },
    pre_compute_height: function (vue_module, in_paras) {
      const Cls = Vue.extend(vue_module)
      const ins = new Cls(in_paras)
      ins.$mount()
      document.body.appendChild(ins.$el)
      var style = window.getComputedStyle(ins.$el)
      var real_height = parseFloat(style.height, 10) + parseFloat(style.marginBottom) +
        parseFloat(style.marginTop) + parseFloat(style.borderTopWidth) +
        parseFloat(style.borderBottomWidth) + parseFloat(style.paddingBottom) +
        parseFloat(style.paddingTop)
      document.body.removeChild(ins.$el)
      real_height = real_height / UnitHandler.get_dpi()[1] * 25.4
      return real_height
    },
    handle_answersheet_structure: function () {
      var areas_with_same_type = []
      var question_index = 0
      var index_in_area
      var area_index
      var last_question
      for (var type_index in this.answersheet_structure.titleModels) {
        const no_arr = this.answersheet_structure.titleModels[type_index].BigTitle.split(',')
        const start_no = parseInt(no_arr[0], 10)
        const end_no = parseInt(no_arr[no_arr.length - 1], 10)
        areas_with_same_type[type_index] = {}
        areas_with_same_type[type_index].big_title = this.complete_type(type_index)
        areas_with_same_type[type_index].questions_division = []
        index_in_area = 0
        area_index = 0
        areas_with_same_type[type_index].questions_division[area_index] = []
        for (var start_tile = start_no; start_tile <= end_no; start_tile++) {
          while (
            question_index !== this.answersheet_structure.structureList.length &&
            this.answersheet_structure.structureList[question_index].BigTile === start_tile
          ) {
            last_question = this.answersheet_structure.structureList[question_index]
            areas_with_same_type[type_index].questions_division[area_index][index_in_area] = last_question
            index_in_area++
            question_index++
            if (
              question_index !== this.answersheet_structure.structureList.length &&
              this.answersheet_structure.structureList[question_index].BigTile <= end_no &&
              last_question.InPosition !== this.answersheet_structure.structureList[question_index].InPosition
            ) {
              area_index++
              areas_with_same_type[type_index].questions_division[area_index] = []
              index_in_area = 0
            }
          }
        }
      }

      return areas_with_same_type
    },
    handle_column_spanner: function (params) {
      var reducer = (total, cur) => total + cur
      var column_index = this.all_parts_dict[params.component._uid].column
      for (var area_index in this.columns_info[column_index]) {
        if (this.columns_info[column_index][area_index].component._uid === params.component._uid) {
          this.delta_height = this.columns_info[column_index][area_index].real_height - params.real_height
          this.columns_info[column_index][area_index].real_height = params.real_height
          break
        }
      }
      var cur_delta_height = Math.abs(this.delta_height)
      var next_delta_height = 0
      var remaing_height = cur_delta_height
      while (column_index !== this.columns_info.length) {
        var dynamic_height = this.columns_info[column_index].map(el => el.real_height).reduce(reducer)
        if (this.delta_height < 0 && dynamic_height < 243) {
          break
        }
        if (this.delta_height > 0 && dynamic_height > 243) {
          break
        }
        console.log('after adjustment, the height of this column is changed to', dynamic_height, 'mm', 'after dragging', next_delta_height, 'mm')
        if (this.delta_height < 0 && dynamic_height > 243) {
          var cur_column_last_area = this.columns_info[column_index][this.columns_info[column_index].length - 1]
          if (this.created_columns.length === (column_index + 1)) {
            var column_tmp = this.render_part(
              AnswersheetColumn,
              {
                propsData: {
                  position_no: this.created_columns.length + 1,
                  paper_size: this.paper_size,
                  columns_num: this.columns_num
                }
              },
              document.getElementById('answersheet_page_1')
            )
            this.created_columns[this.created_columns.length] = column_tmp
            this.columns_info[this.created_columns.length - 1] = []
          } else {
            var next_column_first_area = this.columns_info[column_index + 1][0]
          }

          if (
            cur_column_last_area.component.$options.name === 'AnswersheetObjective' &&
            this.columns_info[column_index + 1].length !== 0 &&
            next_column_first_area.component.$options.name === 'AnswersheetObjective'
          ) {
            console.log('same area spans two columns')
            cur_column_last_area.real_height -= cur_delta_height
            if (cur_column_last_area.real_height < 15) {
              remaing_height -= cur_delta_height + cur_column_last_area.real_height
              next_delta_height += cur_delta_height + cur_column_last_area.real_height
              next_column_first_area.real_height += cur_delta_height + cur_column_last_area.real_height
              this.created_columns[column_index].$el.removeChild(cur_column_last_area.component.$el)
              delete this.all_parts_dict[cur_column_last_area.component._uid]
              this.columns_info[column_index].pop()
              document.getElementById('answersheet_page_1').removeChild(
                this.column_spanner[column_index + 2].$el
              )
            } else {
              cur_column_last_area.component.$el.style.height = cur_column_last_area.real_height + 'mm'
              next_column_first_area.real_height += cur_delta_height
              next_delta_height += cur_delta_height
              remaing_height = 0
            }
            next_column_first_area.component.$el.style.height = next_column_first_area.real_height + 'mm'
          } else if (
            cur_column_last_area.component.$options.name === 'AnswersheetObjective'
          ) {
            console.log('objective area only occupies one column before adjustment')
            if (cur_column_last_area.real_height < 15) {
              this.created_columns[column_index].$el.removeChild(cur_column_last_area.component.$el)
              delete this.all_parts_dict[cur_column_last_area.component._uid]
              this.columns_info[column_index].pop()
              next_delta_height += cur_column_last_area.real_height
            } else {
              cur_column_last_area.component.$el.style.height = (cur_column_last_area.real_height - 15) + 'mm'
              next_delta_height += 15
            }
            remaing_height -= next_delta_height
            var next_columns = this.created_columns[column_index + 1]
            var new_added_component = this.render_part_first(
              AnswersheetObjective,
              {
                propsData: {
                  height: 15
                }
              },
              next_columns.$el
            )
            this.columns_info[column_index + 1].unshift(
              {component: new_added_component, real_height: 15}
            )
            this.all_parts_dict[new_added_component._uid] = {real_height: 15, column: column_index + 1}
            var spanner = this.render_part(
              AnswersheetSpanner,
              {
                propsData: {
                  position_no: column_index + 2,
                  paper_size: this.paper_size,
                  columns_num: this.columns_num
                }
              },
              document.getElementById('answersheet_page_1')
            )
            this.column_spanner[column_index + 2] = spanner
          } else {
            console.log('this area can\'t span two columns')
            this.created_columns[column_index].$el.removeChild(cur_column_last_area.component.$el)
            this.columns_info[column_index].pop()
            this.created_columns[column_index + 1].$el.insertBefore(cur_column_last_area.component.$el, this.created_columns[column_index + 1].$el.firstChild)
            this.columns_info[column_index + 1].unshift(cur_column_last_area)
            this.all_parts_dict[cur_column_last_area.component._uid].column = column_index + 1
            next_delta_height += cur_column_last_area.real_height
            remaing_height -= next_delta_height
          }

          if (remaing_height <= 0) {
            column_index++
            cur_delta_height = next_delta_height
            remaing_height = next_delta_height
            next_delta_height = 0
          }
        } else if (this.delta_height > 0 && dynamic_height < 243) {
          if ((column_index + 1) === this.created_columns.length) {
            return
          }
          cur_column_last_area = this.columns_info[column_index][this.columns_info[column_index].length - 1]
          next_column_first_area = this.columns_info[column_index + 1][0]
          var available_height = 243 - dynamic_height
          if (
            cur_column_last_area.component.$options.name === 'AnswersheetObjective' &&
            next_column_first_area.component.$options.name === 'AnswersheetObjective'
          ) {
            if ((next_column_first_area.real_height - available_height) < 15) {
              if (available_height > next_column_first_area.real_height) {
                cur_column_last_area.real_height += next_column_first_area.real_height
                cur_column_last_area.component.$el.style.height = cur_column_last_area.real_height + 'mm'
                delete this.all_parts_dict[next_column_first_area.component._uid]
                this.created_columns[column_index + 1].$el.removeChild(next_column_first_area.component.$el)
                this.columns_info[column_index + 1].shift()
                document.getElementById('answersheet_page_1').removeChild(
                  this.column_spanner[column_index + 2].$el
                )
              } else {
                return
              }
            } else {
              cur_column_last_area.real_height += available_height
              cur_column_last_area.component.$el.style.height = cur_column_last_area.real_height + 'mm'
              next_column_first_area.real_height -= available_height
              next_column_first_area.component.$el.style.height = next_column_first_area.real_height + 'mm'
            }
          } else if (next_column_first_area.component.$options.name === 'AnswersheetObjective') {
            if ((next_column_first_area.real_height - available_height) < 15) {
              if (available_height > next_column_first_area.real_height) {
                new_added_component = this.render_part(
                  AnswersheetObjective,
                  {
                    propsData: {
                      height: next_column_first_area.real_height
                    }
                  },
                  this.created_columns[column_index].$el
                )
                this.columns_info[column_index].push(
                  {component: new_added_component, real_height: next_column_first_area.real_height}
                )
                this.all_parts_dict[new_added_component._uid] = {real_height: next_column_first_area.real_height, column: column_index}
                cur_column_last_area.real_height += next_column_first_area.real_height
                cur_column_last_area.component.$el.style.height = cur_column_last_area.real_height + 'mm'
                delete this.all_parts_dict[next_column_first_area.component._uid]
                this.created_columns[column_index + 1].$el.removeChild(next_column_first_area.component.$el)
                this.columns_info[column_index + 1].shift()
              } else {
                return
              }
            } else {
              new_added_component = this.render_part(
                AnswersheetObjective,
                {
                  propsData: {
                    height: available_height
                  }
                },
                this.created_columns[column_index].$el
              )
              this.columns_info[column_index].push(
                {component: new_added_component, real_height: available_height}
              )
              this.all_parts_dict[new_added_component._uid] = {real_height: available_height, column: column_index}
              next_column_first_area.real_height -= available_height
              next_column_first_area.component.$el.style.height = next_column_first_area.real_height + 'mm'
              spanner = this.render_part(
                AnswersheetSpanner,
                {
                  propsData: {
                    position_no: column_index + 2,
                    paper_size: this.paper_size,
                    columns_num: this.columns_num
                  }
                },
                document.getElementById('answersheet_page_1')
              )
              this.column_spanner[column_index + 2] = spanner
            }
          } else {
            if (available_height > next_column_first_area.real_height) {
              this.created_columns[column_index + 1].$el.removeChild(next_column_first_area.component.$el)
              this.columns_info[column_index + 1].shift()
              this.created_columns[column_index].$el.appendChild(next_column_first_area.component.$el)
              this.columns_info[column_index].push(next_column_first_area)
              this.all_parts_dict[next_column_first_area.component._uid].column = column_index
            } else {
              return
            }
          }

          if (this.columns_info[column_index + 1].length === 0) {
            document.getElementById('answersheet_page_1').removeChild(this.created_columns[column_index + 1].$el)
            this.created_columns.pop()
            this.columns_info.pop()
          } else {
            column_index++
          }
        } else {
          column_index++
        }
      }
    },
    render_finished: function (params) {
      var reducer = (total, cur) => total + cur
      if (params.component._uid in this.all_parts_dict) {
        this.handle_column_spanner(params)
      } else {
        var current_column = this.created_columns.length - 1
        this.all_parts_dict[params.component._uid] = {real_height: params.real_height, column: current_column}
        var len = this.columns_info[current_column].length
        this.columns_info[current_column][len] = params
        this.columns_info[current_column].sort(function (a, b) {
          return a.component._uid - b.component._uid
        })
        if (this.render_waiting_list.length === 0) {
          return
        }

        var computed_height = this.pre_compute_height(
          this.render_waiting_list[0][0],
          this.render_waiting_list[0][1]
        )
        var cur_occupied_height = this.columns_info[current_column].map(el => el.real_height).reduce(reducer)
        var will_used_height = this.columns_info[current_column].map(el => el.real_height).reduce(reducer, computed_height)
        console.log(will_used_height)
        if (will_used_height < 243) {
          this.render_part(
            this.render_waiting_list[0][0],
            this.render_waiting_list[0][1],
            this.created_columns[this.created_columns.length - 1].$el
          )
          this.render_waiting_list.shift()
        } else {
          if (this.render_waiting_list[0][2]) {
            var first_height = 243 - cur_occupied_height
            if (first_height > 15) {
              this.render_part(
                this.render_waiting_list[0][0],
                {
                  propsData: {
                    height: first_height
                  }
                },
                this.created_columns[this.created_columns.length - 1].$el
              )
              this.render_waiting_list[0][1].propsData.height = 50 - first_height
              var spanner = this.render_part(
                AnswersheetSpanner,
                {
                  propsData: {
                    position_no: this.created_columns.length + 1,
                    paper_size: this.paper_size,
                    columns_num: this.columns_num
                  }
                },
                document.getElementById('answersheet_page_1')
              )
              this.column_spanner[this.created_columns.length + 1] = spanner
              return
            }
          }
          var column_tmp = this.render_part(
            AnswersheetColumn,
            {
              propsData: {
                position_no: this.created_columns.length + 1,
                paper_size: this.paper_size,
                columns_num: this.columns_num
              }
            },
            document.getElementById('answersheet_page_1')
          )
          this.created_columns[this.created_columns.length] = column_tmp
          this.columns_info[this.created_columns.length - 1] = []
          this.render_part(
            this.render_waiting_list[0][0],
            this.render_waiting_list[0][1],
            this.created_columns[this.created_columns.length - 1].$el
          )
          this.render_waiting_list.shift()
        }
      }
    },
    rerender_trigger: function (params) {
      console.log(this.columns_info)
    }
  }
}
</script>

<style>
  @import "../assets/css/answersheet.css";
</style>



