import Vue from 'vue'
import Router from 'vue-router'
import AnswersheetGenerator from '@/components/AnswersheetGenerator'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'AnswersheetGenerator',
      component: AnswersheetGenerator
    }
  ]
})



