<template>
  <div class="a33_column" v-bind:style="{left: left_pos + 'mm',top: top_pos + 'mm'}">
  </div>
</template>

<script>
export default {
  name: 'AnswersheetColumn',
  data () {
    return {
      left_pos: 15,
      top_pos: 26
    }
  },
  props: {
    position_no: Number,
    paper_size: String,
    columns_num: Number
  },
  created: function () {
    this.left_pos = 15
    this.top_pos = 26
    if (this.paper_size === 'A3') {
      if (this.columns_num === 3) {
        this.left_pos = 135 * ((this.position_no - 1) % 3) + 15
        this.top_pos = 296 * Math.floor((this.position_no - 1) / 3) + 26
      }
    }
  },
  mounted: function () {

  },
  updated: function () {
  }
}
</script>



