<template>
  <div class="answer_sheet_answer_area_subjective">
    <table>
      <tr v-for="row_index in row_num" :key="row_index">
        <td v-for="col_index in col_num" :key="col_index">
          <ul class="answer_sheet_answer_area_subjective_subarea">
            <li v-for="problem in slice((col_index - 1) * subarea_options_num  + (row_index - 1) * col_num * subarea_options_num, subarea_options_num)" :key="problem">
              <span class="answer_sheet_answer_area_subtitle">{{problem[0]}} </span><span class="use_answer_sheet_font" v-for="option_index in problem[1]" :key="option_index">{{options[option_index - 1]}}</span>
            </li>
          </ul>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import UnitHandler from '../utils/units.js'

export default {
  name: 'AnswersheetSubjective',
  props: {
    raw_data: Array
  },
  created: function () {
    this.row_num = Math.ceil(this.raw_data.length / (this.col_num * this.subarea_options_num))
    this.problems = []
    if (this.raw_data.length <= this.subarea_options_num) {
      this.col_num = 1
    }
    for (var index in this.raw_data) {
      var detail_attr = this.raw_data[index].InDetail.split('-')
      this.problems[index] = [this.raw_data[index].BigAlias, parseInt(detail_attr[detail_attr.length - 1])]
    }
  },
  data () {
    return {
      row_num: 1,
      col_num: 2,
      subarea_options_num: 5,
      real_height: 0,
      problems: [['1', 5], ['2', 3], ['3', 6], ['4', 2], ['5', 4], ['6', 4], ['7', 4]],
      options: ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'I', 'J', 'K', 'L', 'M', 'N']
    }
  },
  methods: {
    slice: function (start, range) {
      return this.problems.filter(function (item, index) {
        return index >= start && index < start + range
      })
    }
  },
  updated: function () {
    var computed_height = parseFloat(
      document.defaultView.getComputedStyle(this.$el).height)
    this.real_height = computed_height / UnitHandler.get_dpi()[1] * 25.4
    console.log('real height of subjective area' + ' is ' + this.real_height.toFixed(2))
    this.$emit('render_finished', {component: this, real_height: this.real_height})
  }
}
</script>

<style>
  .answer_sheet_answer_area_subjective_subarea {
    list-style-type: none;
    margin-bottom: 1mm;
    margin-top: 1mm;
    padding-left: 2mm;
  }

  .answer_sheet_answer_area_subjective {
    width: 100%;
  }

  .answer_sheet_answer_area_subjective > table{
    width: 100%;
    border-collapse: collapse;
    border: 1px solid black;
    text-align: left;
    table-layout: fixed;
  }

  .answer_sheet_answer_area_subjective > table > tr {
    border-collapse: collapse;
    border: 1px solid black;
  }

  .answer_sheet_answer_area_subjective > table > tr > td {
    border-collapse: collapse;
    border: 1px solid black;
    vertical-align: top;
  }

  .answer_sheet_answer_area_subtitle {
    font-size: 3mm;
  }
</style>



