<template>
  <div class="answer_sheet_answer_area_objective"  ref="problem_objective" @mousedown="init_drag" v-bind:style="{height: height + 'mm'}">
    <div class="resizer"></div>
  </div>
</template>

<script>
import UnitHandler from '../utils/units.js'

export default {
  name: 'AnswersheetObjective',
  props: {
    height: Number
  },
  data () {
    return {
      start_x: 0,
      start_y: 0,
      start_width: 0,
      start_height: 0,
      real_height: 0
    }
  },
  methods: {
    init_drag: function (e) {
      this.start_x = e.clientX
      this.start_y = e.clientY
      this.start_width = parseInt(document.defaultView.getComputedStyle(this.$el).width, 10)
      this.start_height = parseInt(document.defaultView.getComputedStyle(this.$el).height, 10)
      document.documentElement.addEventListener('mousemove', this.do_drag, false)
      document.documentElement.addEventListener('mouseup', this.stop_drag, false)
    },
    do_drag: function (e) {
      this.$el.style.width = this.start_width + 'px'
      this.$el.style.height = (this.start_height + e.clientY - this.start_y) + 'px'
      var computed_height = parseFloat(
        document.defaultView.getComputedStyle(this.$el).height) + 2
      this.real_height = computed_height / UnitHandler.get_dpi()[1] * 25.4
      this.$emit('render_finished', {component: this, real_height: this.real_height})
    },
    stop_drag: function (e) {
      document.documentElement.removeEventListener('mousemove', this.do_drag, false)
      document.documentElement.removeEventListener('mouseup', this.stop_drag, false)
    }
  },
  updated: function () {
    var computed_height = parseFloat(
      document.defaultView.getComputedStyle(this.$el).height) + 2
    this.real_height = computed_height / UnitHandler.get_dpi()[1] * 25.4
    console.log('real height of objective area' + ' is ' + this.real_height.toFixed(2))
    this.$emit('render_finished', {component: this, real_height: this.real_height})
  }
}
</script>

<style>
  .answer_sheet_answer_area_objective {
    width: 100%;
    height: 50mm;
    border: 1px solid black;
    position: relative;
  }

  .answer_sheet_answer_area_objective:hover .resizer {
    display: block;
  }

  .resizer {
    width: 17px;
    height: 17px;
    position:absolute;
    right: 0;
    bottom: -9px;
    cursor: move;
    background-image: url("../../assets/img/icons8-split-vertical-50.png");
    background-size: 100% 100%;
    display: none;
  }
</style>



