<template>
  <div class="answer_sheet_title">
    <span>{{title}}</span>
  </div>
</template>

<script>
import UnitHandler from '../utils/units.js'

export default {
  name: 'AnswersheetTitle',
  props: {
    title: String
  },
  data () {
    return {
      real_height: 0.0
    }
  },
  mounted: function () {
  },
  updated: function () {
    var computed_height = parseFloat(
      document.defaultView.getComputedStyle(this.$el).height)
    this.real_height = computed_height / UnitHandler.get_dpi()[1] * 25.4 + 12
    console.log('real height of answersheet title' + ' is ' + this.real_height.toFixed(2))
    this.$emit('render_finished', {component: this, real_height: this.real_height})
  }
}
</script>

<style>
  .answer_sheet_title {
    margin-top: 2mm;
    margin-bottom: 10mm;
    font-size: 7mm;
    font-weight: bold;
    text-align: center;
  }
</style>



