<template>
  <div class="answer_sheet_problem_title">
    {{title}}
  </div>
</template>

<script>
import UnitHandler from '../utils/units.js'

export default {
  name: 'AnswersheetProblem',
  props: {
    title: String
  },
  data () {
    return {
      real_height: 0.0
    }
  },
  updated: function () {
    var computed_height = parseFloat(
      document.defaultView.getComputedStyle(this.$el).height)
    this.real_height = computed_height / UnitHandler.get_dpi()[1] * 25.4 + 2
    console.log('real height of problem' + ' is ' + this.real_height.toFixed(2))
    this.$emit('render_finished', {component: this, real_height: this.real_height})
  }
}
</script>

<style>
  .answer_sheet_problem_title {
    text-align: left;
    font-size: 3mm;
    margin-bottom: 1mm;
    margin-top: 1mm;
  }
</style>



